'use client'
import React from 'react'
import styles from './navbar.module.scss'
import { useRouter } from 'next/navigation'
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faBell, faCircleUser } from '@fortawesome/free-solid-svg-icons'
import NotificationBell from '../notificacionBell/notificacionBell';


export default function Navbar() {

    const router = useRouter()

    function bell() {
        <NotificationBell />
    }

    function logout() {
        router.push('/')
    }

    return (
        <nav className={styles.navbar}>
            <div className="title">
                Geticket - Gestión de Tickets
            </div>
            <ul>
                <li><a href="#">Tickets</a></li>
                <li><a href="#">Reportes</a></li>
                <li><FontAwesomeIcon icon={faBell} size='lg' /></li>
                <li onClick={logout}>Cesar De Dios <FontAwesomeIcon icon={faCircleUser} size='lg' /></li>
            </ul>
        </nav>
    )
}
